<style lang="scss" scoped>
*{
  border: none;
  margin: 0;
  padding: 0;
}
.forumPage{
  position: absolute;
  top: 0;
  left: 0;
  width:100%;
  height:100%;
  overflow:hidden;
  background:white;
  .forumType{
    width:6.9rem;
    height:0.87rem;
    margin:0 0.3rem;
    font-size:0.32rem;
    line-height:0.87rem;
    color:#666666;
    li{
      float:left;
      width:1.5rem;
      font-weight: bold;
      &.active{
        color:#1797c6;
      }
    }
  }
  #forumListScroll{
    position: absolute;
    top: 1.61rem;
    left: 0;
    right:0;
    bottom:0.88rem;
    margin:auto;
    overflow:hidden;
  }
  hr{
    width:100%;
    height: 0.02rem;
    background: #f2f2f2;
    }
  .sendPostBtn{
    position:fixed;
    bottom:0;
    left:0;
    height:0.88rem;
    width:100%;
    line-height:0.88rem;
    text-align:center;
    background:url('../../assets/images/publishBtn.jpg') no-repeat;
    background-size:cover;
    color:white;
    font-size:0.36rem;
    font-weight: bold;
  }
}
</style>




